import React, {useState} from 'react';
import './MainFon.sass'
import '../../../assets/style/style.sass'
import ModalWindow from './ModalWindow'


let MainFon = () => {

    const [openM, setOpenM] = useState(false)

    return (
        <div className='main-block'>
            <div className='container'>
                <div className='content-main-block'>
                    <h1>Сайт для иностранных граждан, осуществляющих трудовую деятельность в Беларуси</h1>
                </div>
                <div className='Btn-block'>
                    <a href='tel:+375447048228'><button className='Btn-for-call'>Позвонить</button></a>
                    
                    <div className='div-for-Btn-for-write'>
                        
                        {openM ? <ModalWindow setOpenM={setOpenM} className='modal-wrapper'/> : <button onClick={() => setOpenM(!openM)} className='Btn-for-write'>Написать</button>}

                        {openM && <ModalWindow setOpenM={setOpenM} className='modal-wrapper'/> && <button className='Btn-for-write'>Написать</button> || <ModalWindow setOpenM={setOpenM} className='inheritM'/>}
                    </div>
                </div>
            </div>
        </div>
    )
}
    
export default MainFon
