import React from 'react'
import './Services.sass'
import {useCookies}  from 'react-cookie'
import '../../../assets/style/style.sass'
import servicesText from '../../../assets/services/servicesText'
import Service from './Service'
import { NavLink } from 'react-router-dom'
import Heart from '../../../assets/img/heart.png'


const Services = (props) => {

    // const [cookie, setCookie] = useCookies(['points'])

    // let tttt = () => {
    //     alert('qqqqqqqqqqq')
    //     setCookie('points' + 1)
    // }
    return (
        <section className='services-block'>
            <div className='container'>
                <div className='wrapper-services-block'>
                    {servicesText.map((serv, index) => (
                        <div className='service-block'> 
                            <NavLink key={index} to={'service/' + serv.id}>
                                <h2>{serv.name}</h2>
                            </NavLink>
                            <p>{serv.text}</p>
                            <div className='block-for-like'>
                                <span>{serv.likesCount}</span>
                                <img src={Heart}/>
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        </section>
    )
}
    
export default Services