import React from 'react'
import { NavLink } from "react-router-dom"

const Article_4 = () => {
    return (
        <div className='article-text-block'>
            <p>Последнее десятилетия в нашей стране действительно происходит смена эпох – с лидирующих позиций в сфере предоставления информации уходит печатная продукция, и рынок начинает покорять всемирная информационная компьютерная сеть – Интернет. Безусловно, масштабы поставляемого ей контента не представляется возможным оценить, однако его качество и источник все-таки стоит контролировать с целью защиты авторского права каждого. 
            В настоящей статье мы хотим обратить внимание на такой объект авторского права, как веб-сайт, так как именно он является главным ресурсом информации в сети Интернет и заказы на его создание приобретают все большую популярность. 
            Самостоятельный термин «веб-сайт» либо иное синонимичное ему понятие не имеет трактовки в Законе № 262-З «Об авторском праве и смежных правах» (далее – Закон), однако мы предполагаем, что он непосредственно относится к объектам авторского права (с учетом, что их перечень не является исчерпывающим), проанализировав пп. 1 и 2 ст. 6 Закона, по следующим причинам.
            К объектам авторского права предъявляется два основных требования:
            1.	существование объекта в какой-либо объективной форме. В нашем случае веб-сайт имеет свое выражение в электронной форме;
            2.	являться результатом творческой деятельности. Насколько веб-сайт является творческим «продуктом» сейчас и необходимо оценить.
            В Республике Беларусь под веб-сайтом понимается совокупность веб-страниц, физически находящихся на одном веб-сервере и объединенных одним именем, общей тематикой и системой навигации (это расшифровка из подп. 3.3.9 п. 3.3 государственного стандарта Республики Беларусь СТБ 1693-2009).
            Итак, мы имеем множество страниц, одну тематику, единый дизайн, базовую структуру навигации, программный код, информационные блоки, содержащие узконаправленный контент и еще множество элементов. Будет ли все это признаваться объектом авторского права? Да, но только если все вышеописанные элементы создавались человеком самостоятельно, без использования шаблонных элементов, уже имеющихся в сети Интернет, с индивидуальной разработкой цветового решения сайта, его системы навигации. То есть именно веб-сайт, который будет обладать такими характеристиками, как творческая новизна, индивидуальность и неповторимость может быть признан результатом творческой деятельности.
            Не стоит забывать о самом наполнении веб-сайта, так как все видео-, фотоматериалы, тексты, изображения, гиперссылки на другие веб-сайты являются самостоятельными объектами авторского права и если вы будете использовать их без ведома самого автора вам может быть предъявлен иск.
            Теперь, когда мы определили, что веб-сайт – это все-таки объект авторского права, на который есть как личные имущественные права автора, так и имущественные, стоит рассмотреть порядок его создания, например, профессиональным юридическим лицом для какой-либо компании или индивидуальным предпринимателем по их заказу.
            Важно знать, что личные неимущественные права принадлежат автору независимо от его имущественных прав и сохраняются за ним в случае перехода его имущественных прав на результаты интеллектуальной деятельности к другому лицу. На практике, организация, создавшая сайт по договору, для подтверждения своего авторства указывает знак охраны, состоящий из 3 элементов: латинской буквы «С»: ©, имени правообладателя, даты первого опубликования. Такая методика охраны является достаточно распространённой и популярной.
            Итак, между двумя организациями заключается договор, в котором есть такие стороны, как заказчик и подрядчик, главный акцент в котором должен быть сделан не только на описание создаваемого сайта, но и на порядок предоставления имущественных прав на веб-сайт. Сама природа договора больше подобна смешанной, так как представляет по структуре договор подряда (работа + результат в установленный срок) с элементами лицензионного договора либо договора уступки исключительного права. Кстати говоря, в настоящее время постоянно ведутся дискуссии как на белорусской, так и российской платформах о том, к какому виду договора отнести создание веб-сайта. 
            В большинстве своем, заказчику важен сам результат, который он получает на исходе, но не процесс самой разработки. Его цель – получение в установленные сроки готовый веб-сайт с характеристиками, предусмотренными договором. Если же плюс к разработке компания оказывает какие-либо консультационные услуги, то да, здесь будут также возникать элементы договора возмездного оказания услуг. В таких случаях необходимо совместить в договоре все обязательные нормы, присущие каждому отражающемуся в нем виду договора, и тогда мы имеем право совместись весь комплекс и работ, и услуг в одном соглашении – договоре.
            Не стоит забывать, что кроме таких условий, как сроки выполнения работ, предмет договора, цена оплаты, сторонам необходимо определиться каким образом будут переданы права на созданный объект интеллектуальной собственности. В данному пункте договора стоит предусмотреть кому будут принадлежать имущественные права после создания веб-сайта, в какой момент происходит их передача, кто имеет право носить какие-либо корректировки или изменения, и кто отвечает за последующее контентное наполнение сайта.
            Недостаточно урегулированная часть белорусского законодательства в области создания такого объекта авторского права, как веб-сайт имеет две стороны, как положительную, так и отрицательную. С одной стороны, ты вправе сам определять какие условия включать в договор на свое усмотрение и что выгодно тебе, как стороне исполнителя либо заказчика. С другой – при возникновении спора между сторонами судебная система имеет не совсем достаточную квалификацию и опыт для справедливого рассмотрения дела о нарушении чьих-либо прав, либо неисполнение условий по договору на разработку сайта.
            В заключение отметим, что в случае необходимости мы поможем Вам составить проект договора на разработку объекта интеллектуальной собственности, в частности сайта, которым Вы сможете пользоваться в процессе всей дальнейшей деятельности, в котором будут полностью соответствовать Вашим интересам. Если вы выступаете в качестве заказчика на создание сайта мы также можем ознакомиться с проектом договора, который Вам предлагает исполнитель, на соответствие его Вашим интересам. В случае, если корректировка договора займёт у нашего сотрудника менее 30 минут, то данная услуга будет Вам предоставлена бесплатно.</p>
        </div>
    )
}

export default Article_4