import React from 'react'
import './Tears.sass'

const Tears = () => {
    return (
        <section className='tears-block'>
            <div className='container'>
                <div className='wrapper-tears-block'>
                    <h2>Специальная программа "Бизнес без слёз"</h2>
                    <p>Специалисты нашей компании предоставляют профессиональные услуги физическим и юридическим лицам для решения различных споров в отношениях с государственными органами, организациями, а также при оформлении различных документов и справок. Вы можете рассчитывать на помощь юриста нашей компании в любой непредвиденной ситуации.</p>
                    <p>Взаимоотношения между частными лицами, организациями и органами государственной власти регламентированы различными нормативными документами. Для грамотной и эффективной защиты собственных интересов лицо обязано обладать соответствующими знаниями в юридической сфере. Результатом самостоятельного решения важных вопросов при отсутствии требуемых навыков являются излишние временные и финансовые затраты.</p>
                    <p>Если к Вам должен прийти налоговый инспектор или работник Департамента финансовых расследований, Госстандарта, а Вы не знаете нужно ли подписывать акт проверки, что писать в протоколе об административном правонарушении и как вести себя с работниками государственных органов, то лучше обратитесь к нам. Ведь проблему лучше предотвратить на стадии возникновения, чем потом обжаловать документ в котором говориться о том, что проверяемый субъект со всем согласен.</p>
                    <p>Если же в отношении Вас или Вашей организации уже есть акт проверки, протокол об административном нарушении либо постановление об административном правонарушении и даже если Вас могут привлечь в качестве подозреваемого (обвиняемого) ещё не поздно это исправить и обойтись малыми потерями.</p>
                    <p>При возникновении необходимости Вы можете в любое время позвонить нам и мы приедем в любую точку Беларуси до проведения проверки и подготовим Вас к её проведению, а также будем представлять Ваши интересы непосредственно перед государственными органами. Сотрудники государственных органов часто пользуются незнанием субъектами хозяйствования тонкостей законодательства, поэтому часто допускают нарушения при проведении проверок, неправильно квалифицируют деяния.</p>
                    <p>Данной услуги до настоящего момента не было на рынке, все юридические фирмы предлагали лишь обжаловать действия должностных лиц государственных органов, что чаще всего не приносило никакого результата, за исключением дополнительных затрат за составление бумаг, которые были нужны лишь для успокоения клиента, чтобы он думал, что сделал всё что возможно. Однако при сотрудничестве с нами Вы оплачиваете наши услуги после получения положительного результата, а именно после возврата товара, после отмены постановления, которым предусматривалось взыскание штрафа и иное.</p>
                    <p>Несите ответственность лишь за те деяния, которые совершили, и соразмерно при доказанности Вашей вины. Каждый клиент, которому требуется помощь, может рассчитывать на индивидуальный подход к решению его проблемы и полную конфиденциальность.</p>
                </div>
            </div>
        </section>
    )
}
    
export default Tears