import React from 'react'
import { NavLink } from "react-router-dom"
import '../../../assets/style/style.sass'
import Api from './Api'
import './Articles.sass'
import Article_1 from './ArticlesText/Article_1'
import Article_2 from './ArticlesText/Article_2'
import Article_3 from './ArticlesText/Article_3'
import Article_4 from './ArticlesText/Article_4'
     
const Articles = () => {


    return (
        <div className='container'>
            <div className='wrapper-articles-page'>
                <div className='api'>
                    <Api />
                </div>
                <div className='wrapper-articles'>
                    <div className='articles-block'>

                    <div className='article one'>
                        <NavLink to={'article_1/'}>
                            <h2>
                                Как работать в Беларуси иностранному гражданину
                            </h2>
                        </NavLink>
                        <Article_1 />
                        <NavLink to={'article_1/'} className='read-next'>
                            <span>Читать далее...</span>
                        </NavLink>
                    </div>

                        <div className='sp-block'>

                            <div className='article two'>
                                <NavLink to={'article_2/'}>
                                    <h2>Объект розничной торговли</h2>
                                </NavLink>
                                    <Article_2 />
                                <NavLink to={'article_2/'} className='read-next'>
                                    <span>Читать далее...</span>
                                </NavLink>
                            </div>

                            <div className='article three'>
                                <NavLink to={'article_3/'}>
                                    <h2>Ах уж эти проверки…</h2>
                                </NavLink>
                                <Article_3 />
                                <NavLink to={'article_3/'} className='read-next'>
                                    <span>Читать далее...</span>
                                </NavLink>
                            </div>
                            
                        </div>


                        <div className='article four'>
                            <NavLink to={'article_4/'}>
                                <h2>Веб-сайт как объект авторского права</h2>
                            </NavLink>
                            <Article_4 />
                            <NavLink to={'article_4/'} className='read-next'>
                                <span>Читать далее...</span>
                            </NavLink>
                        </div>
                    </div>                    
                </div>
                 
            </div>
        </div>
    )
}

export default Articles