import React, { useState } from 'react'
import { NavLink } from "react-router-dom"


const Article_2 = () => {
    return (
            <div className='article-text-block'>
                <p>Каждый мечтает о прибыльном деле, однако не каждый знает, что за этим стоит. В данной статье мы раскроем основные «подводные камни» открытия своего розничного магазина с учетом требований нашего законодательства.
                Итак, что же такое торговля в розницу? Розничная торговля – вид торговли, связанный с приобретением и продажей товаров для личного, семейного, домашнего и иного подобного использования, не связанного с предпринимательской деятельностью, если иное не установлено законодательными актами Республики Беларусь.
                Если Вы приняли решение об открытии своего личного магазина и уже нашли помещение для аренды, тогда вам стоит обратить внимание подпадает ли вид реализуемой вами продукции под действие Положения о лицензировании. Если же Вы, к примеру, собираетесь реализовывать изделия из драгоценных металлов и драгоценных камней, алкогольные напитки или табачные изделия, тогда Вам необходимо обратиться в лицензирующий орган для получения соответствующего разрешения.
                Далее стоит ознакомиться с ч. 1 подп. 3.5 п. 5 Декрета №7, в соответствии с которым необходимо соблюдать общие требования пожарной безопасности, санитарно-эпидемиологические требования, требования в области охраны окружающей среды, требования в области ветеринарии к содержанию и эксплуатации капитальных строений (зданий, сооружений), изолированных помещений и иных объектов. При их несоблюдении естественно предусмотрена ответственность для субъектов хозяйствования, так что лучше ознакомиться.
                Например, если рассматривать требования пожарной безопасности, предъявляемые к помещениям субъектов хозяйствования, стоит отметить ряд обязанностей. В-первую очередь, если торговый объект располагается в арендуемом помещении, ответственность за обеспечение его пожарной безопасности прописывается в договоре аренды. На руководителя организации возлагаются такие обязанности, как разработка инструкции по пожарной безопасности, обеспечение пожарной безопасности и противопожарного режима, содержание в работоспособном и исправном состоянии средств противопожарной защиты и пожаротушения и т.д., установка стендов с информацией о пожарной безопасности и их своевременное обновление (микроорганизации и индивидуальные предприниматели освобождаются от такой обязанности).
                Теперь о самом важном: каждый магазин обязан осуществлять свою деятельность, а конкретно прием наличных денежных средств с использованием:
                А) Кассового оборудования – от вас требуется проведение денежной суммы через кассу и выдача покупателю кассового чека. Деньги, полученные от клиента, необходимо поместить в ящик для денег кассового оборудования либо при его отсутствии – в иное установленное место.
                Б) Платежного терминала – в настоящий момент обязательно для всех субъектов, осуществляющих торговлю в павильонах, киосках, магазинах-складах, торговых объектах, расположенных в капитальных строениях (зданиях и сооружениях).
                В) СКНО – специальное электронное устройство для сбора, обработки и передачи информации о денежном обороте посредством сотовой подвижной электросвязи в центр обработки данных СККО. Цель данной системы – передача в режиме мобильной связи суммы выручки организации в налоговый орган. До осуществления деятельности должен быть заключён договор на установку СКНО. Сроки установки, актуальные в данный момент, будут указаны далее. 
                Порядок подключения системы СКНО проходит в 2 этапа: 
                1) Заключение гражданско-правового договора с РУП «Информационно-издательский центр по налогам и сборам». Подписание договора обязательное требование, так как является точкой отсчета для установки системы.
                2) Непосредственно установка СКНО – обязательно в настоящее время для торговых объектов с торговой площадью 650 кв. м. и более. Для юридических лиц обязательство возникает – в городах областного подчинения и Минске – с 1 мая 2019 года, в городах районного подчинения – с 1 сентября 2019 года, на всей территории Беларуси – с 1 октября 2019 года.
                Данные требования особенно важны, так как их исполнение находится под пристальным контролем налоговых органов (а значит чревато штрафами для организации – ст. 12.20 КоАП). 
                Одним из альтернативных модернизированных средств, с помощью которого можно проводить кассовые операции, является программная касса. Касса представляет собой программное обеспечение, которое автоматически будет передавать данные в систему контроля кассового оборудования. Оно может быть установлено двумя способами: либо на электронное устройство (ноутбук, планшет, мобильный телефон), либо через центр обработки данных оператора программной кассовой системы.
                Использование подобного альтернативного решения имеет свои требования: приобретение ключа электронной цифровой подписи, устройства для печати платежного документа, платежный терминал, оплата услуг оператора программной кассовой системы. С другой стороны, субъект хозяйствования освобождается от уплаты залоговой стоимости на СКНО, что является хорошим способом экономии. Так, организация вправе самостоятельно определять какой вид кассы – кассовый аппарат или программная касса более выгодный и походящий для нее.
                Следующее, о чем стоит помнить – это отдельные требования, предъявляемые для магазинов розничной торговли: 
                1.	Каждая организация, намеревающаяся осуществлять деятельность в сфере торговли, обязана подать уведомление о начале ее осуществления в местный исполнительный и распорядительный орган по месту нахождения юридического лица с помощью подачи письменного документа через службу «одно окно» или направления его заказным почтовым отправлением с уведомлением о вручении либо с использованием единого портала электронных услуг путем проставления электронной цифровой подписи. Со дня, следующего за днем направления уведомления, субъект хозяйствования вправе начать осуществление своей деятельности. Кроме того, организации необходимо также внести сведения о торговых объектах, для включения их в Торговый реестр. Подача такого рода заявления осуществляется таким же способами, как описано выше, в местный исполнительный и распорядительный орган. За совершение данной процедуры плата не взымается.
                2.	В настоящее время субъектам хозяйствования нет необходимости согласовывать режим работы розничных торговых объектов, объектов торговых центров. Однако, если Вы планируете осуществлять розничную торговлю в промежуток с 23:00 до 7:00 все-таки согласование с местными исполнительными и распорядителями органами необходимо в рамках внесения сведений в Торговый реестр.
                На каждой из двери магазина должна быть информация о режиме работы Вашего магазина, а также наименование организации, если же наименование организации не совпадает с названием магазина – обязательно необходимо указать наименование и тип розничного объекта.
                3.	Каждый субъект хозяйствования обязан иметь книгу замечаний и предложений (заполненную в соответствии инструкцией!) в доступном для потребителя месте и указать лицо, ответственное за ее хранение, а также ее постоянное месторасположение. Кроме того, необходимо наличие в организации книги учета проверок, а также документов, подтверждающих приобретение (поступление) товаров и документов о качестве и безопасности товаров в случаях, установленных законодательством Республики Беларусь.
                4.	Продавец также обязан довести до сведения покупателя любым доступным способом:
                	информацию о номере специального разрешения (лицензии), сроке его действия, гос. органе, выдавшем их;
                	информацию о месте нахождения продавца (также ФИО руководителя, его контактный номер), если оно не совпадает с местом нахождения торгового объекта, объекта общественного питания;
                	если продавец – индивидуальный предприниматель – также информацию о его гос. регистрации и наименовании органа, осуществившего его гос. регистрацию в качестве индивидуального предпринимателя, месте жительства;
                	права потребителя, предоставленные ему законодательством (в данном случае будет уместно разместить в магазине Закон Республики Беларусь от 09.01.2002 № 90-З «О защите прав потребителей»);
                	дату внесения сведений о торговом объекте, объекте общественного питания в Торговый реестр (не стоит забывать об обязанности внести данные сведения в Торговый реестр!).
                Если при осуществлении своей деятельности Вы осуществляете реализацию продукции, – Вы являетесь субъектом технического нормирования и стандартизации, а значит обязаны соблюдать действующее в государстве законодательство в данной сфере, а также требования технических регламентов Таможенного Союза. 
                5.	Продавец обязан обеспечить наличие в магазине единообразно оформленных ценников на реализуемые товары с указанием наименования товара, а также подписи лица, уполномоченного продавцом и даты оформления ценника.
                6.	Необходимо разработать и принять ассортиментный перечень товаров, реализуемых организацией исходя из ассортимента предлагаемых к продаже товаров на основании перечня товаров, утвержденного Министерством торговли.
                В данном случае необходимо помнить два момента: в отдельных случаях при продаже товаров не требуется обязательное наличие и утверждение ассортиментного перечня товаров – это может быть полезной информацией для вас; при разработке данного перечня отдельным требованием является наличие товаров отечественного производства – минимальное количество указывается в утвержденном Министерством торговли перечне. 
                7.	Работник продавца должен иметь нагрудный знак (так называемый бедж) с указанием собственного имени и должности. По усмотрению продавца на нагрудном знаке также указываются фамилия и отчество (если имеется) работника.
                Такие строгие, но достаточно обоснованные требования предъявляются к субъектам, ориентированным на продажу товаров в розницу. Стоит понимать, что Вы работаете с покупателем и он является Вашим клиентом, приносящим прибыль. Лучше лишний раз соблюсти правила бесплатно, чем позже все равно их исполнить, но уже потеряв достаточную сумму на штрафы.
                Раскрытые в данной статье аспекты довольно полно освещают требования, которые предъявляются к магазинам розничной торговли. Однако, не секрет, что на практике возникают различные ситуации, при которых все-таки необходимо обратиться за помощью к профессиональным юридическим компаниям, имеющим достаточный опыт в данной сфере. Мы с радостью предложим Вам свои услуги, если вы все-таки решили открыть свой бизнес в торговле и получать от нее достойную прибыль.</p>
            </div>      
    )
}

export default Article_2