import React from 'react'
import '../../../assets/style/style.sass'
import './Problems.sass'
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
    },
    heading: {
        fontSize: theme.typography.pxToRem(25),
        fontWeight: theme.typography.fontWeightRegular,
    },
}));

export default function SimpleAccordion() {
    const classes = useStyles();

    return (
        <div className='container'>
            <div className='wrapper-for-accordion'>
                <div className={classes.root}>
                    <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel1a-content"
                        id="panel1a-header"
                    >
                        <Typography className={classes.heading}>Что нужно сделать иностранному гражданину при прибытии в Республику Беларусь?</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography>
                            В первую очередь, иностранному гражданину требуется оформить пребывания. Для удобства иностранному гражданину при обращении к специалистам нашей организации необходимо лишь предоставить фото паспорта, после чего в кратчайшие сроки Вам будет выдан вкладыш с регистрацией временного пребывания сроком до 90 календарных дней.
                            При повторной регистрации временного пребывания, а также обращении в государственные органы по вопросам пребывания (проживания) специалисты нашей организации дают консультации с письменными инструкциями и заполнением соответствующих документов для совершения той или иной административной процедуры.
                            Во-вторых, для посещения государственных органов иностранному гражданину необходим легальный перевод паспорта. Наша организация обеспечивает также при наличии оригинала паспорта в течение рабочего дня заверенный в установленном порядке перевод паспорта.
                        </Typography>
                    </AccordionDetails>
                    </Accordion>
                    <Accordion>
                    <AccordionSummary
                        expandIcon={<ExpandMoreIcon />}
                        aria-controls="panel2a-content"
                        id="panel2a-header"
                    >
                        <Typography className={classes.heading}>Что делать, если иностранного гражданина незаконно привлекли к ответственности и ущемили его права?</Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                        <Typography>
                            Специалисты нашей организации имеют многолетний опыт работы, в том числе по урегулированию разногласий с государственными органами в части применения законодательства, регулирующего правовое положение иностранных граждан.
                            Если Вы считаете, что Ваши права были ущемлены действиями должностных лиц, например, милиции, специалисты нашего предприятия помогут восстановить нарушенные права.
                        </Typography>
                    </AccordionDetails>
                    </Accordion>
                </div>
            </div>
        </div>
    );
}
        
