import React from 'react'
import { NavLink } from "react-router-dom"

const Article_3 = () => {
    return (
        <div className='article-text-block'>
            <p>Прошло совсем не много времени с того момента как в 2012 году в Указ Президента Республики Беларусь от 16.10.2009 № 510 «О совершенствовании контрольной (надзорной) деятельности в Республике Беларусь» (далее – Указ) внесли изменения и добавили в него очень «современное и модное» слово мониторинг. 
            Слово, как мы уже сказали, «современное» а вот исполнять, применять и использовать новый метод поручили людям «не современным и не модным», т.е. тем, кто годами доставал из «широких штанин» свою «красную корочку», и со словами «сейчас мы Вас закроем» делали, как они считали, очень нужную работу по наведению порядка среди комерсантов.
            И вот «уважаемый» главный инспектор главного управления самой главной инспекции вместо приостановления работы организации, изъятия всех имеющихся на складе товаров, в один день превратился из «грозы морей и океанов» в обычного служащего, который в соответствии с Указом должен помочь «комерсанту» в его нелегком деле  по зарабатыванию денег, путем разъяснения требований законодательства (направление рекомендаций), предупреждения об ответственности и иными способами не связанными с «отрубанием головы».
            «Наконец-то….» вздохнули все «комерсанты» и пошли регистрировать новые организации. «Рано радуетесь…» проворчал главный инспектор главного управления главной инспекции потянувшись за книжечкой с надписью «ПИКоАП».
            А теперь немножечко суровой реальности…
            В настоящее время работниками некоторых контролирующих органов разработана новая схема выполнения показателей по пополнению бюджета от административных штрафов при манипулировании «размытыми» нормами действующего законодательства.
            Одной из таких норм является Положение о порядке проведения мониторинга (далее   Положение), утвержденного Указом. В соответствии с данным нормативным правовым актом мониторинг как форма контроля (надзора) заключается в наблюдении, анализе, оценке и установлении причинно-следственных связей. Он применяется для оперативной оценки фактического состояния объектов и условий деятельности субъекта мониторинга с точки зрения соответствия требованиям законодательства.
            Следует отметить, что нормативно-прикладная модель проведения мониторинга фактически нивелирует его главный отличительный признак, позволяющий разграничить мониторинг и проверку. Этот признак – невозможность использования полномочий, предоставленных контролирующим (надзорным) органам и их должностным лицам, при проведении проверок. Действительное же предназначение мониторинга заключается в его дистанцировании от проверки. Использование механизма мониторинга является переходом от проверки к оценке спорных ситуаций, предупреждению возможных негативных последствий и минимизации налоговых рисков.
            «Положение о порядке проведения мониторинга», утвержденное Указом, входит в структуру административного законодательства, которое отличается своим императивным регулированием и в отношении субъектов публичной власти применяется такой комплекс общих разрешений и общих запретов, который идентифицируется в известной формуле «можно только то, что разрешено законом».
            Согласно абз. 1 ч. 1 п. 8 Положения по результатам мониторинга составляется аналитическая записка и принимается решение руководителя органа о применении мер ответственности. И получается так, что теперь работник контролирующего органа вправе начать административный процесс только после решения руководителя органа о применении мер ответственности, а так как из доказательств самого нарушения у него может быть только аналитическая записка, составленная в ходе анализа и наблюдения (пункт 2 Положения), то доказательства самого правонарушения у него фактически отсутствуют. 
            И вот наш главный инспектор главного управления самой главной инспекции открывает книжку с надписью «ПИКоАП» и что же он видит…
            Согласно п. 3 ч. 1 ст. 9.1 ПИКоАП Республики Беларусь поводом к началу административного процесса является непосредственное обнаружение признаков административного правонарушения судом, органом, ведущим административный процесс.
            Согласно ч. 2 ст. 9.1. ПИКоАП Республики Беларусь основаниями для начала административного процесса являются достаточные данные указывающие на признаки административного правонарушения, предусмотренного Особенной части КоАП Республики Беларусь, если при этом отсутствуют обстоятельства, исключающую административный процесс.
            Согласно ст. 9.5 ПИКоАП Республики Беларусь при наличии поводов и оснований для начала административного процесса административный процесс считается начатым с момента составления протокола об административном правонарушении либо составления протокола о процессуальном действии.
            ….и вот теперь его не остановить!!!
            В ходе мониторинга только заметив нарушение он достает свой «любимый» протокол осмотра и протокол опроса и начинает административный процесс.
            Он чувствует «необычайную легкость»!!! Ведь раньше чтобы использовать все эти бланки процессуальных документов у него должно было быть предписание на проведение проверки по результатам которой он составлял акт (который кстати можно было обжаловать), а сейчас полная свобода действий!!! Удивленный «комерсант» не может понять, что случилось проверки нет, а протокол процессуального действия есть!!!
            Давайте попробуем разобраться что происходит. Должностное лицо контролирующего органа имеет право действовать лишь в пределах своей компетенции, которая в указанной ситуации (проводится мониторинг) установлена требованиями Указа, как специального акта, который имеет верховенство по отношению к общим (КоАП, ПИКоАП), что закреплено в ст. 10 Закона Республики Беларусь от 10.01.2000 № 361-З «О нормативных правовых актах». 
            С учетом изложенного, требования Положения к действиям должностного лица при проведении мониторинга лишают его возможности использования полномочий, предоставленных им при проведении проверки и не позволяют производить действия в соответствии с ПИКоАП!!!
            К сожалению не все контролирующие органы могут правильно применять «новейшие» методы контрольной работы в виде мониторинга, а некоторые даже и не понимают, для чего вообще взят курс руководством нашей страны на «либерализацию» бизнеса…</p>
        </div>
    )
}

export default Article_3