import React from 'react'
import { NavLink } from "react-router-dom"

const Article_1 = () => {
    return (
        <div className='article-text-block'>
            <p>
                Как работать в Беларуси иностранному гражданину
                Если вы организация, которая желает принять на работу иностранного гражданина либо вы и есть иностранный гражданин, который хочет работать в Республике Беларусь, то вам придётся столкнуться с белорусскими реалиями и получением разрешения на работу. Далее в статье рассмотрим всю процедуру получения разрешения на работу для иностранных граждан.
                Нанимателю, намеревающемуся использовать труд иностранцев, не имеющих разрешений на постоянное проживание в Республике Беларусь, в количестве, превышающем десять человек, необходимо получить разрешение на привлечение иностранной рабочей силы
                Так, разрешение на работу не требуется в следующих случаях:
                	иностранец, имеющий разрешения на постоянное проживание в Республике Беларусь;
                	для граждан Российской Федерации, Республики Казахстан, Республики Армения и Кыргызской Республики;
                	получившие высшее образование в учреждениях образования Республики Беларусь и трудоустраивающиеся по полученной специальности и присвоенной квалификации в течение 1 года после окончания обучения;
                	являющиеся победителями (лауреатами) национальных (международных) конкурсов, отмеченных наградами в сфере их профессиональной деятельности;
                	если нанимателем является резидент Парка высоких технологий.
                Есть определённые особенности трудоустройства граждан Украины. Так существует мнение, что гражданин Украины вправе работать лишь у одного нанимателя и на одном рабочем месте, прямого указания на то нет, но на практике сотрудники управления по гражданству и миграции откажут в выдаче разрешения на работу гражданину Украины, в случае, если на него уже получено разрешение на работу этим же либо другим нанимателем. При принятии на работу граждан Украины, проживавших на территории Донецкой и Луганской областей Украины не менее года наниматель освобождён от уплаты госпошлины. И срок принятия решения о выдаче - 5 рабочих дней.
                Общий порядок принятия на работу иностранного гражданина:
                1.	Прежде чем подать заявление о выдаче спецразрешения необходимо разместить вакансию (вакансия направляется на бланке определённой формы) в органе по труду, занятости и социальной защиты по месту будущего осуществления трудовой деятельности. Исключением является принятие на работу на должность директора иностранного гражданина, являющегося участником данной организации, в данном случае размещать вакансию не требуется. Данная вакансия должна быть размещена вот на этом сайте http://vacancy.mintrud.by/ru/applicant/search-jobs/?search_type=extended и находится она там должна не менее 15 рабочих дней.
                2.	Подать заявление о выдаче спецразрешения установленной формы, к которому прилаживается копия нотариально заверенного перевода паспорта (в некоторых управлениях достаточно копии самого паспорта либо только копии паспорта и его перевода без нотариального заверения), документ, подтверждающий уплату госпошлины в размере 5 базовых величин (122,50 бел. Руб.) (можно оплачивать через ЕРИП, но такой способ платежа не принимается, например, в УГИМ Гродненской области). Срок рассмотрения заявления – 15 календарных дней.
                Отметим, что нередко государственные органы пытаются убедить в том, что работник, принимаемый на определённую должность, должен соответствовать квалификационным характеристикам соответствующей должности, например, директор должен иметь высшее образование и стаж работы на руководящих должностях не менее 5 лет. Однако данный подход в корне не верен, что подтверждается и самим Министерством труда и социальной защиты Республики Беларусь, и наниматель может принять на работу сотрудника, который не соответствует квалификационным характеристикам.
                Специальное разрешение выдаётся на 1 год и может быть продлено один раз, после чего нужно будет получать ещё одно разрешение. При обращении за продлением действия специального разрешения заполняется такое же заявление и к нему прилаживается дополнительно оригинал специального разрешения, на котором будет стоять отметка о продлении.
                3.	После получения специального разрешения в течение месячного срока необходимо зарегистрировать трудовой договор с работником. Данная процедура осуществляется бесплатно. Помимо сведений, которые должны быть указаны согласно Трудовому кодексу Республики Беларусь, договор с иностранцем должен содержать следующие сведения:
                	порядок, основания прекращения, изменения и продления трудового договора;
                	условия переезда в Республику Беларусь, питания, проживания, медицинского обслуживания, в частности за чей счёт это будет производиться;
                	место осуществления трудовой деятельности, а это значит, что должен быть указан конкретный адрес, по которому будет находиться иностранец; Является интересным то, что данное положение в законодательстве не закреплено, однако без данного условия Ваш трудовой договор зарегистрирован не будет;
                	номер, срок действия и орган выдавши специальное разрешение на работу;
                	если договор составлен на русском языке, то необходимо указание на то, что русский язык является понятным работнику.
                Интересным является и тот факт, что прямого ограничения установления работнику-иностранцу подвижного и разъездного характера работы, производства работы вахтовым методом, постоянной работы в пути, работы вне места жительства (полевое довольствие), и даже работы в двух городах одновременно законодательством не установлено, однако на практике трудовой договор, содержащий подобные условия, должностными лицами зарегистрирован не будет.
                Срок для регистрация трудового договора законодательно не установлен. Практика складывается таким образом, что различные областные управления самостоятельно определяют срок регистрации: это может быть 1, 7 дней либо в тот же день.
                На практике и поэтому в каждой области он различен. В Минске этот срок составляет, например, 7 дней, а в Минской области договор отдадут уже на следующий день, а в Бресте зарегистрируют и отдадут в этот же день.
                Наниматель обязан вернуть специальное разрешение в подразделение по гражданству и миграции в течение пяти рабочих дней со дня:
                	прекращения трудового договора с трудящимся-иммигрантом;
                	получения уведомления об аннулировании специального разрешения;
                	истечения срока действия специального разрешения.
                Наниматель также должен вернуть специальное разрешение, если трудовой договор с трудящимся-иммигрантом так и не будет заключен.
                При смене юридического адреса (не в рамках одной области) организации нанимателю необходимо обратиться в управление по гражданству и миграции по новой подведомственности за заменой специальных разрешений. Данная процедура осуществляется бесплатно. Установленной формы заявления о внесении изменений в специальное разрешение не установлено, но рекомендуем указывать в нём всё максимально подробно иначе должностные лица не раз отправят Вас переписывать.
                Выше приведена общая формула получения разрешения на работу для трудящегося-иммигранта и всё кажется простым и понятным….. А вопросы возникают, когда есть желание подать заявление о выдаче спецразрешения или зарегистрировать трудовой договор с указанием в нём места осуществления трудовой деятельности, отличного от того, который был указан в вакансии или заявлении. И тут на Вас должностные лица государственных органов начинают смотреть как на самого главного в мире обманщика….. Ещё проблематичней дело обстоит с подачей документов на получение годового проживания… Здесь Вы столкнётесь с местной властью, которая Вам в лицо сообщит желает она видеть на «своей территории» данных иностранных граждан либо нет.
                Будьте в дальнейшем осторожны с теми трудовыми функциями, которые будет осуществлять трудящийся-мигрант, так, если администратор будет разлаживать товар, либо упакует потребителю товар, а ещё не дай Бог он будет работать во время своего обеда либо задержится на работе, и эти сведения станут известны сотрудникам органов по гражданству и миграции – ждите протокол. 
                Однако, всё может кардинально отличаться в зависимости от обстоятельств. Поэтому если Вам необходимо принять на работу иностранного гражданина либо Вы и есть иностранный гражданин, которому нужна помощь для того чтобы законно осуществлять трудовую деятельность в нашей стране, можете обратиться к нам для получения консультации, которую Вам не дадут ни в одном государственном органе. Также мы поможем с оформлением документов для получения специального разрешения на работу и временного проживания, и будем сопровождать Вас в дальнейшем.
            </p>
        </div>
    )
}

export default Article_1