import React, {useState} from 'react';

function ModalWindow(props) {
let www = () => {
    props.setOpenM(false)
    alert('Спасибо за Ваше обращение. В течение нескольких часов наш специалист свяжется с Вами!')
}
    return (
        <div className={props.className}>
            <button onClick={() => props.setOpenM(false)} className='button-modal-close'>X</button>
            <div className='modal-content'>
                <span><input type='text' placeholder='Ваше имя*' size="40" /></span>
                <span><input type='tel' size='40' data-mask='+375 (__) ___-__-__' placeholder='+375 (__) ___-__-__'/></span>
                <span><input type='email' size='40' placeholder='E-mail'/></span>
                <span className='for-textarea'><textarea cols='40' rows='10' placeholder='Ваш вопрос'/></span>
                <button onClick={() => www()}>Направить вопрос</button>
            </div>
        </div>          
    )
}
export default ModalWindow