import React, {useState} from 'react';
import './Nav.sass'
import { NavLink } from 'react-router-dom'
import '../../assets/style/style.sass'

const Nav = (props) => {
    return (
        <div>
            <nav className={props.className}>
                <NavLink href='#page-1' to="/services" className='li'>Услуги</NavLink>
                <NavLink href='#page-2' to="/articles" className='li'>Статьи</NavLink>
                <NavLink href='#page-3' to="/problems" className='li'>Проблемы иностранцев</NavLink>
                <NavLink to="/about" className='li'>О нас</NavLink>
            </nav>
        </div>
   
    )
}
    
export default Nav