import React, { useEffect, useState } from 'react'
import '../../../assets/style/style.sass'
import './Articles.sass'
     
const Api = () => {
    let [error, setError] = useState(null);
    let [isLoaded, setIsLoaded] = useState(false);
    let [items, setItems] = useState([]);

    function randomInteger() {
        let rand = 1 + Math.random() * 1641;
        return Math.round(rand);
      }
      let sss = randomInteger()

    useEffect(() => {
        fetch("https://type.fit/api/quotes")
        .then(res => res.json())
        .then(
            (result) => {
                setIsLoaded(true);
                setItems(result);
            },
            (error) => {
                setIsLoaded(true);
                setError(error);
            }
        )
    }, []) 

    if (error) {
        return <div>Error: {error.message}</div>;
    } else if (!isLoaded) {
        return <div>Loading...</div>
    } else {
        return (
            <div className="item-api"><span>{items[sss] && items[sss].text}</span><span className='author'>{items[sss] && items[sss].author}</span></div>
        )
    }
}

export default Api