import React, {useState} from 'react';
import './Header.sass'
import Logo from '../../assets/img/Logo.png'
import { NavLink } from 'react-router-dom'
import '../../assets/style/style.sass'
import Menu from '../../assets/img/menu.png'
import Nav from '../Nav/Nav'
import Delete from '../../assets/img/icon-delete.png'

const Header = () => {
    const [open, setOpen] = useState(false)
   
    return (
        <div className='header-block'>
            <div className=' container'>
                <div className='wrapper-header'>
                    <NavLink to="/home"><img className='logo' src={Logo}/></NavLink>
                    <div>
                        <div onClick={() => setOpen(!open)}>
                            {open ? <img className="delete" src={Delete}/> : <img className="menu" src={Menu}/>}
                            
                            {open && <Nav className='ppp'/> || <Nav className='inherit'/>}
                        </div>  
                    </div>
                </div>
            </div>
        </div>
    )
}
    


export default Header